html{scroll-behavior:smooth}*{box-sizing:border-box;margin:0;padding:0;font-family:'Lato', sans-serif}.containerPage{margin:0;padding:0}body{background-color:#14081f}

.navbar{background-color:#000000;height:100vh;width:5vw;display:flex;justify-content:center;position:fixed;float:left;z-index:4}.menu-arrow{font-size:3.5vw;background:none}.nav-menu{background-color:#000000;width:30vw;display:flex;justify-content:center;align-items:center;position:fixed;top:0;left:-100%;transition:850ms;z-index:4}.nav-menu-items{width:100%;height:100%}.nav-menu.active{left:0;transition:350ms;z-index:4}.nav-text{display:flex;justify-content:flex-start;align-items:center;padding:8px 0px 8px 16px;list-style:none;height:10vh}.nav-text a{text-decoration:none;color:#f5f5f5;font-size:3vw;width:100%;height:100%;display:flex;align-items:center;padding:0 16px;border-radius:4px}.nav-text a:hover{background-color:#2F3146}.navbar-toggle{background-color:#000000;width:100%;height:100vh;display:flex;justify-content:flex-start;align-items:center;padding-left:3vw}span{margin-left:16px}

.lowerImagesParent{width:100vw}.gamesImages{position:relative}.gamesImages1{position:relative;right:6.5%;bottom:2.3vw;width:17%}.gamesImages2{position:relative;right:8.5%;top:8vw;width:12%}.gamesImages3{position:relative;left:2%;top:5vw;width:12%}.gamesImages4{position:relative;left:12%;top:3vw;width:12%}.gamesImages5{position:relative;left:13%;bottom:3vw;width:21%}.separatorMountains{height:31vw}.mountains{position:absolute;left:0px;width:100vw;top:21vw}.home{background-color:#29103F;display:grid;place-items:center;text-align:center;height:auto;justify-content:center}.dragon-right{margin:0 2vw 0;width:7vw}.dragon-left{margin:0 2vw 0;width:7vw;-webkit-transform:scaleX(-1);transform:scaleX(-1)}

.aboutMe{background-color:#14081f;width:95vw;height:auto;padding-bottom:10vh}.containerPadding1{margin:0;padding:0;width:95vw}.title_story{margin-top:2vh;padding-bottom:3vh;text-align:right}.containerPadding2{margin:0;padding:0;width:100vw;padding-left:4vw;display:inline-flex}.imageContainer{display:grid;place-items:center;margin-top:-10vh;padding:0;flex:1 1}.profilePic{border-radius:50%;width:40vw;height:40vw}.paragraphStory{width:50%;padding-right:2vw;display:inline-block;font-size:2vw;color:#7554A3;text-align:right}.titleParagraphStory{color:#99DE1E;font-size:3vw}.singleParagraphStory{font-size:2vw;color:#7554A3;text-align:right}

.title{margin-bottom:0;color:#E9E2F5;font-size:8vw;font-family:'Roboto Serif', serif;font-weight:bold}.subtitle{color:#7554A3;font-family:'Roboto Slab', serif;font-weight:normal;font-size:3.5vw}.text{color:#7554A3;font-family:'Roboto Slab', serif;font-weight:normal;font-size:3.5vw}.barFrame{background-color:grey;justify-content:left;margin-left:30px;padding:0px;width:90%;border-radius:25px}.barValue{background-color:#D5DCE6;border-radius:25px}.center{text-align:center;align-items:center}.aling-Left{text-align:left;align-items:left}.footer{margin-bottom:100px}

.content{position:relative;height:40vw;width:95vw;margin-top:-40px;margin-left:3vw}.content__background,.content__background__shadow,.content__background__image,.content__background__video,.content__area{position:absolute;top:0;bottom:0}.content__background{left:0;right:0}.content__background__shadow{left:0;background:#000;width:30%;z-index:2}.content__background__shadow:before{content:'';position:absolute;z-index:10;background-image:linear-gradient(to right, #000, transparent);top:0;bottom:0;left:100%;width:100%}.content__background__image{right:0;width:70%;height:100%;background-position:center 10%;background-size:cover;z-index:1}.content__background__video{right:0;width:auto;height:100%;background-position:center 10%;background-size:cover;z-index:1}.content__area{left:0;right:0;height:100%;z-index:3;transition:height 2s}.content__area__container{color:wheat;height:100%}.content__title{font-size:5vw;color:#fff;font-weight:700}.content__description{font-size:2vw;color:#999;margin-top:5px;max-width:30vw}.content__close{color:#fff;width:40px;height:40px;background:transparent;outline:none;border:none;position:absolute;top:30px;right:30px}

.slider-wrapper{padding:40px 0;overflow:hidden;position:relative}

.slider{display:flex;position:relative;margin-left:5vw}.slider__container{display:flex;transition:-webkit-transform 300ms ease 100ms;transition:transform 300ms ease 100ms;transition:transform 300ms ease 100ms, -webkit-transform 300ms ease 100ms;z-index:3;width:100%}.slider:not(.slider--open) .item:hover{-webkit-transform:scale(1.3) !important;transform:scale(1.3) !important}.slider:not(.slider--open):hover .item{-webkit-transform:translateX(-25%);transform:translateX(-25%)}.slider:not(.slider--open) .item:hover ~ .item{-webkit-transform:translateX(25%);transform:translateX(25%)}

.show-details-button{position:absolute;bottom:0;left:0;right:0;opacity:0;transition:opacity 300ms ease 100ms;border:0;outline:none;width:100%;height:100%}.show-details-button span{display:block;width:14px;margin:0 auto;color:white}

.item{flex:0 0 20.7vw;transition:-webkit-transform 300ms ease 100ms;transition:transform 300ms ease 100ms;transition:transform 300ms ease 100ms, -webkit-transform 300ms ease 100ms;margin:0 2px;position:relative}.item img{height:100%;width:70%;vertical-align:top;border-radius:1ch}

.portafolio{background-color:#14081f;display:flex;text-align:center;height:200%;margin-left:2vw;justify-content:center;padding-bottom:10%}.title_journey{padding-left:5vh;text-align:left}.subtitle_portfolio{padding-left:9vh;text-align:left}

.contactMe{background-color:#14081f;display:flex;text-align:center;width:auto;padding-left:5vw;height:70vw}.containerPage{margin:0;text-align:center;align-items:center;align-content:center}.contactButtonsContainer{width:100%}.contactButtons{margin:0 5vw 0;font-size:10vw}.title_looking{margin-top:5vh;padding-bottom:3vh;text-align:center}

.sliderCarousel{text-align:center;width:70vw;left:20vw;margin:0;justify-content:center;padding-bottom:10%}.textCarousel{width:100%;height:100%;display:flexbox;text-align:center}.carousel-item.active,.carousel-item-next,.carousel-item-prev{display:block}

